body {
    background: linear-gradient(to left, #651fff, #a255ff);
    text-align: center;
    font-family: cursive;
    width: 100%;
}

div {
    padding:10px;
}

h1{
    color: white;
}

#App {
    padding: 1%;
    
}

.tutorial {
    position: absolute;
    color: white;
    background: #343a40;
    width: 80%;
    left: 8%;
    opacity: 0.9;
    margin: 5px auto;
    font-family: sans-serif;
}

.upperBox {
    width: 20px;
    height: 20px;
    background: white;
    font-weight: 600;
    border:1px solid black;
    margin: 5px;
    cursor: pointer;
}

.upperBox:active {
    background: #ffc107;
}

.lowerBox {
    width: 30px;
    height: 20px;
    color: white;
    font-weight: 600;
    background: #0100ca;
    border:1px solid white;
    margin: 5px;
    cursor: pointer;
}

.lowerBox:active {
    background: white;
    color: black;
}

.hint {
    color: white;
}

.container1 {
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 7px 7px 18px;
    background-color: #651fff;
}

.container2 {
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 7px 7px 18px;
    background-color: #651fff;
    flex-flow: wrap;
}

.hintcontainer {
    border: 1px solid white;
    margin: 10px auto;
    box-shadow: 7px 7px 18px;
}

button {
    color: white;
    font-weight: bold;
    padding: 5px;
    background: #0100ca;
    border:2px solid white;
    border-radius: 10px;
    box-shadow: 2px 2px 5px black;
    cursor: pointer;
}

button:hover{
    color: black;
    background: white;
}

button:active{
    color: red;
    background: white;
    border-color: red;
}

.taunt {
    opacity: 0;
    background: black;
    font-size: 0em;
    color: white;
    width: 50px;
    height:12px;
    marginLeft: 0px
    
}